.AboutScreen{
    height: 100vh;
    width: 100vw;
    display: flex;
}
.AboutContainer{
    width: 100%;
}
.AboutTitle{
    display: flex;
    font-size: 6.5em;
    margin-top: 50px;
    margin-left: 80px;
    color: rgb(220, 132, 132);
}
.line{
    width: 100px;
    height: 3px;
    margin-left: 80px;
    margin-top: 20px;
    background-color: #73cadc;
}
.AboutDiscription{
    font-size: 20px;
    margin-left: 80px;
    margin-top: 30px;
}
.Technologies{
    display: flex;
    margin-left: 100px;
    margin-top: 20px;
}
.Technologies ul{
    columns: 3;
    gap: 30px;
}
.drop-in {
    animation: drop-in 1s ease 200ms backwards;
}

.drop-in-2 {
    animation: drop-in 500ms ease 800ms backwards;
}
.drop-in-3 {
    animation: drop-in 1500ms ease 1000ms backwards;
}
.drop-in-4 {
    animation: drop-in 2500ms ease 1200ms backwards;
}
.drop-in-5 {
    animation: drop-in 3500ms ease 800ms backwards;
}


@keyframes drop-in {
    from {
        opacity: 0;
        transform: translateY(-100px);
    }

    to {
        opacity: 1;
        transform: translate(0px);
    }
}